// common mixins - start

@mixin edgtfBckImageStyle() {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
}

@mixin edgtfImageOverlayHoverStyle($with-hover: true) {
	
	@if ($with-hover) {
		
		&:hover {
			
			&:after {
				opacity: 1;
			}
		}
		
		&:after {
			@include edgtfAbsoluteHolderLayout();
			content: '';
			background-color: rgba($additional-background-color, .4);
			opacity: 0;
			transition: opacity .2s ease-in-out;
		}
		
	} @else {
		@include edgtfAbsoluteHolderLayout();
		content: '';
		background-color: rgba($additional-background-color, .4);
		opacity: 0;
		transition: opacity .2s ease-in-out;
	}
}

@mixin edgtfStandardPaginationStyle($list_type: null) {
	@include edgtfRelativeHolderLayout(top);
	margin: 11px 0 0;
	clear: both;
	
	ul {
		@include edgtfRelativeHolderLayout(top);
		padding: 0;
		margin: 0;
		list-style: none;
		text-align: center;
		
		li {
			font-family: $default-heading-font;
			font-size: 14px;
			letter-spacing: 0.1em;
			font-weight: 600;
			position: relative;
			display: inline-block;
			vertical-align: top;
			margin: 0 6px;
			text-transform: uppercase;
			
			a {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0;
				padding: 0 6px;
			}
			
			&.edgtf-pag-active {
				
				a {
					color: $first-main-color;
				}
			}
			
			&.edgtf-pag-prev,
			&.edgtf-pag-next,
			&.edgtf-pag-first,
			&.edgtf-pag-last {
				margin: 0 7px;
				
				a {
					font-size: 14px;
					
					span {
						display: block;
						line-height: inherit;
						
						&:before {
							display: block;
							line-height: inherit;
						}
					}
				}
			}
			
			@if ($list_type == 'shortcode') {
				
				&.edgtf-pag-prev {
					
					a {
						opacity: 0;
					}
				}
				
				&.edgtf-pag-next {
					
					a {
						opacity: 1;
					}
				}
				
			} @else if ($list_type == 'shop') {
				span {
					position: relative;
					display: inline-block;
					vertical-align: top;
					margin: 0;
					padding: 0;
					color: $first-main-color;
				}
				
				a {
					
					&.next,
					&.prev {
						font-size: 0;
						line-height: 0;
						
						&:before {
							display: block;
							font-size: 14px;
							line-height: 26px;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
					}
					
					&.prev {
						margin-right: 3px;
						
						&:before {
							content: "Prev";
						}
					}
					
					&.next {
						margin-left: 3px;
						
						&:before {
							content: "Next";
						}
					}
				}
			}
		}
	}
}

@mixin edgtfButtonDefaultStyle() {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: auto;
	margin: 0;
	font-family: $default-heading-font;
	font-size: 17px;
	line-height: 2em;
	font-weight: 600;
	letter-spacing: 0.1em;
	text-transform: uppercase;
	outline: none;
	box-sizing: border-box;
	transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
}

@mixin edgtfButtonSize($size: medium) {
	
	@if ($size == 'small') {
		padding: 12px 65px;
		
	} @else if ($size == 'medium') {
		padding: 13px 71px;
		
	} @else if ($size == 'large') {
		padding: 13px 76px;
		
	} @else if ($size == 'huge') {
		display: block;
		width: 100%;
		text-align: center;
		padding: 13px 26px;
	}
}

@mixin edgtfButtonTransparentColor() {
	color: $default-text-color;
	background-color: transparent;
}

@mixin edgtfButtonSolidColor($important: null) {
	color: #fff $important;
	background-color: $first-main-color $important;
	border: 2px solid transparent $important;
}

@mixin edgtfButtonSolidHoverColor($important: null) {
	color: #fff $important;
	background-color: $second-main-color $important;
	border-color: transparent $important;
}

@mixin edgtfButtonOutlineColor($important: null) {
	color: #fff $important;
	background-color: transparent $important;
	border: 2px solid $first-main-color $important;
}

@mixin edgtfButtonOutlineHoverColor($important: null) {
	color: #fff $important;
	background-color: $first-main-color $important;
	border-color: $first-main-color $important;
}

@mixin edgtfButtonClipPathMain($important: null) {
	clip-path: polygon(0 0, calc(100% - 20px) 0, 100% 20px, 100% 100%, 20px 100%, 0 calc(100% - 20px)) $important;
}

@mixin edgtfButtonClipPathOtherSide($important: null) {
	clip-path: polygon(20px 0, 100% 0, 100% calc(100% - 20px), calc(100% - 20px) 100%, 0 100%, 0 20px) $important;
}

@mixin edgtfButtonClipPathTopRight($important: null) {
	clip-path: polygon(0 0,calc(100% - 20px) 0,100% 20px,100% 100%,0 100%,0 0) $important;
}

@mixin edgtfButtonClipPathTopLeft($important: null) {
	clip-path: polygon(20px 0, 100% 0, 100% 100%, 100% 100%, 0 100%, 0 20px) $important;
}

@mixin edgtfButtonClipPathBottomRight($important: null) {
	clip-path: polygon(0 0,100% 0,100% calc(100% - 20px),calc(100% - 20px) 100%,0 100%,0 0) $important;
}

@mixin edgtfButtonClipPathBottomLeft($important: null) {
	clip-path: polygon(0 0, 100% 0, 100% 0, 100% 100%, 20px 100%, 0 calc(100% - 20px)) $important;
}

@mixin edgtfButtonClipPathLines($important: null) {
	
	&:before,
	&:after {
		content: "";
		position: absolute;
		background-color: $first-main-color $important;
		width: 60px;
		height: 2px;
		transform: rotate(45deg);
	}
	
	&:before {
		top: 0;
		right: -14px;
	}
	
	&:after {
		bottom: 0;
		left: -14px;
	}

	.edgtf-edge &,
	.edgtf-ms-explorer & {

		&:before,
		&:after {
			display: none;
		}
	}
}

@mixin edgtfPlaceholder {
	&::-webkit-input-placeholder {
		@content
	}
	
	&:-moz-placeholder {
		@content;
		
		opacity: 1;
	}
	&::-moz-placeholder {
		@content;
		
		opacity: 1;
	}
	&:-ms-input-placeholder {
		@content
	}
}

@mixin edgtfBoxShadows($important: null) {
	filter: drop-shadow(0 3px 7px rgba(14,8,18,.44)) $important;

	.edgtf-safari & {
		filter: none $important;
	}
}

// common mixins - end

