// mixins styles - start

%input-style {
	position: relative;
	width: 100%;
	margin: 0 0 #{$input-margin}px;
	padding: #{$input-vertical-padding}px #{$input-horizontal-padding}px;
	font-family: inherit;
	font-size: 15px;
	font-weight: inherit;
	line-height: calc(#{$input-height}px - (#{$input-vertical-padding * 2}px - 2px));
	color: $input-text-color;
	background-color: $additional-background-color;
	border: none;
	border-radius: 0;
	outline: 0;
	cursor: text;
	-webkit-appearance: none;
	box-sizing: border-box;
	transition: border-color 0.2s ease-in-out;
	@include edgtfBoxShadows();
	
	&:focus {
		color: $input-text-color;
	}
	
	@include edgtfPlaceholder {
		color: inherit;
	}
}

// mixins styles - end

