// heading mixins - start

@mixin edgtfH1() {
	font-size: 80px;
	line-height: 1em;
	letter-spacing: -0.02em;
}

@mixin edgtfH2() {
	font-size: 60px;
	line-height: 1em;
	letter-spacing: -0.02em;
}

@mixin edgtfH3() {
	font-size: 35px;
	line-height: 1em;
	letter-spacing: -0.02em;
}

@mixin edgtfH4() {
	font-size: 25px;
	line-height: 1em;
	letter-spacing: 0;
}

@mixin edgtfH5() {
	font-size: 20px;
	line-height: 1.05em;
	letter-spacing: 0.02em;
}

@mixin edgtfH6() {
	font-size: 14px;
	line-height: 1.43em;
	color: $first-main-color;
	font-weight: 600;
	letter-spacing: 0.1em;
}

@mixin edgtfHeadingStyle($with-heading: null, $with-color: true) {
	
	font-family: $default-heading-font;
	font-weight: 700;
	text-transform: uppercase;
	
	@if ($with-color) {
		color: $default-heading-color;
	}
	
	@if ($with-heading == 'h1') {
		@include edgtfH1();
	} @else if ($with-heading == 'h2') {
		@include edgtfH2();
	} @else if ($with-heading == 'h3') {
		@include edgtfH3();
	} @else if ($with-heading == 'h4') {
		@include edgtfH4();
	} @else if ($with-heading == 'h5') {
		@include edgtfH5();
	} @else if ($with-heading == 'h6') {
		@include edgtfH6();
	}
}

@mixin edgtfDefaultHeadingStyle() {
	@include edgtfHeadingStyle();
	margin: 25px 0;
	
	-ms-word-wrap: break-word;
	word-wrap: break-word;
	
	a {
		@include edgtfTypographyLayout();
		
		&:hover {
			color: $first-main-color;
		}
	}
}

@mixin edgtfBody() {
	font-family: $default-text-font;
	font-size: 15px;
	font-weight: 400;
	line-height: 26px;
	color: $default-text-color;
	background-color: $default-background-color;
	-webkit-font-smoothing: antialiased;
}

// heading mixins - end

